body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.rs-picker-daterange-menu {
  opacity: 1!important;
}
.Assignedshift .card-subtitle {
  font-size: 14px;
}
.media img {
    height: 65px;
    border-radius: 50%;
}
.hourinfo {
  position: absolute;
  right: 25px;
}
.fade.modal.show {
  opacity: 1;
}

/* hide and show crud icons */
.toshowHover { 
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
  height: 100%;
  z-index: 9;
  display:none;
}
.card-body:hover .toshowHover {
  display: block;
}
.badgecontainer {
  position: absolute;
  right: 5px;
}
.backindexs {
  z-index: -1;
  height: 0%;
}
.SmoothEntire {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.draghere {
  z-index: 999;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 70%;
}
.dropableGrow {
  background: #eee;
}
.minsize{
  min-height: 100px;
}